<template>
 <h1 style="text-align:center;">Edit Data</h1>
  <div class="card " style="width: 30rem; margin-left:390px; margin-top: 50px;">
    <div class="card-body" v-if="article">     
        <form @submit.prevent="add">
          <div class="mb-3">
            <label for="exampleInputPassword1" class="form-label">Title</label>
            <input type="text" class="form-control" v-model="article.title" id="exampleInputPassword1">

         
        </div>
        <div class="mb-3">
            <label for="exampleInputPassword1" class="form-label">Category</label>
            <select class="form-control" v-model="article.category">
                <option>-- pilih -- </option>
                <option value="MOBILE LEGENDS" >MOBILE LEGENDS</option>
                <option value="VALORANT" >ALORANT</option>
                <option value="PUBG" >MOBILE LEGENDS</option>
                <option value="LEAGUE OF LEGENDS" >LEAGUE OF LEGENDS</option>
                <option value="DOTA 2" >DOTA 2</option>
            </select>       
          </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Short Description</label>
            <input type="text" class="form-control" v-model="article.short_description">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Description</label>
            <input type="text" class="form-control" v-model="article.description">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Banner</label>
            <input type="text" class="form-control" v-model="article.banner">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Author</label>
            <input type="text" class="form-control" v-model="article.author">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Date</label>
            <input type="text" class="form-control" v-model="article.date">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Time</label>
            <input type="text" class="form-control" v-model="article.time">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Media 1</label>
            <ckeditor :editor="editor" v-model="article.media_1" ></ckeditor>
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Media 2</label>
            <ckeditor :editor="editor" v-model="article.media_2" ></ckeditor>
           
        </div>
        <button type="button" @click="update(article)">Update</button>
        </form>

    </div>
</div>


 
    <!-- <div id="canvas" style="border:1px solid #000000;width: 600px;height: 200px;">
        <div v-html=user.name id="app">
        </div>
    </div> -->








  </template>
  
  <script>
  import getArticle from '../composables/getPost'
  import axios from 'axios'
  import Editor from 'ckeditor5-custom-build/build/ckeditor';

  export default{ 
    data(){
    return{
        editor: Editor,}
    },
      props: ['id'],
      setup(props) {
          const { article, error, load } = getArticle(props.id)
  
          load()
  
          return { article, error }
      },
      methods: {
        update(article){
        return axios.put('https://my-json-server.typicode.com/farhanade40/api/articles/' + article.id , {
          title: this.article.username, 
          category: this.article.category, 
          short_description: this.article.short_description,
          description: this.article.description, 
          banner: this.article.banner,
          author: this.article.author, 
          date: this.article.date,
          time: this.article.time, 
          media_1: this.article.media_1,
          media_2: this.article.media_2
        }).then(res => {
        alert('berhasil update')
        window.location.assign("http://localhost:8080/")
      }).catch((err) => {
        console.log(err);
        
      })
    }
      }
      }
  </script>
  
