<template>
  <h1 style="text-align:center;">Tambah Data</h1>
  <div class="card " style="width: 70rem; margin-left:100px; margin-top: 50px;">
    <div class="card-body">     
        <form @submit.prevent="add">
  
        <div class="mb-3">
            <label for="exampleInputPassword1" class="form-label">Title</label>
            <input type="text" class="form-control" v-model="form.title" id="exampleInputPassword1">

         
        </div>
        <div class="mb-3">
            <label for="exampleInputPassword1" class="form-label">Category</label>
            <select class="form-control" v-model="form.category">
                <option>Choose Province</option>
                <option value="MOBILE LEGENDS" >MOBILE LEGENDS</option>
                <option value="VALORANT" >MOBILE LEGENDS</option>
                <option value="PUBG" >MOBILE LEGENDS</option>
                <option value="LEAGUE OF LEGENDS" >LEAGUE OF LEGENDS</option>
                <option value="DOTA 2" >DOTA 2</option>
            </select>       
          </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Short Description</label>
            <input type="text" class="form-control" v-model="form.short_description">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Description</label>
            <input type="text" class="form-control" v-model="form.description">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Banner</label>
            <input type="text" class="form-control" v-model="form.banner">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Author</label>
            <input type="text" class="form-control" v-model="form.author">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Date</label>
            <input type="date" class="form-control" v-model="form.date">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Time</label>
            <input type="time" class="form-control" v-model="form.time">
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Media 1</label>
            <ckeditor :editor="editor" v-model="form.media_1" ></ckeditor>
           
        </div>
        <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">Media 2</label>
            <ckeditor :editor="editor" v-model="form.media_2" ></ckeditor>
           
        </div>
        

        <button type="submit" class="btn btn-primary">Submit</button>
        </form>
    </div>
</div>


</template>

<script>
    import axios from 'axios'
  
    import Editor from 'ckeditor5-custom-build/build/ckeditor';

export default {
  data(){
    return{
        editor: Editor,
        

      form: {
          id: '',
          title: '',
          category: '',
          short_description: '',
          description: '',
          banner: '',
          author: '',
          date: '',
          time: '',
          media_1: '',
          media_2: ''

        

        },
        articles: '',
        updateSubmit: false
    }
    
  },
  mounted(){
    this.load()
  },
  methods: {
    
    add(){
      axios.post('https://my-json-server.typicode.com/farhanade40/api/articles/', this.form).then(res => {
          this.load()   
          this.form.title = ''
          this.form.short_description = ''
          this.form.description = ''
         
          
          

      }
      
      )
      alert('input berhasil')
             window.location.assign("http://localhost:8080/")
     
 }
}
}
</script>

